import React, { FC, memo } from "react";

import "./CheckBox.scss";

export const FieldCheckBox: FC<{
  name: string;
  title: string;
  register: any;
}> = memo(({ name, title, register, ...props }) => {
  return (
    <div className="fieldCheckBox">
      <label>{`${title}: `} </label>
      <input type="checkbox" name={name} ref={register({})} {...props} />
    </div>
  );
});
